import React from 'react';
import Header from '../common/header/Header';

const PrivacyPage = () => {
  return (
    <div>
      <Header />
      <div className="container mt-5 mb-5">
        <h2>Privacy Policy</h2>
        <p>
          Your privacy is important to us. It is Stuttgart GAA&apos;s policy to
          respect your privacy regarding any information we may collect from you
          across our website,{' '}
          <a href="http://stuttgartgaa.de">http://stuttgartgaa.de</a>, and other
          sites we own and operate.
        </p>
        <h4>Formspree</h4>
        <p>
          We use{' '}
          <a
            href="https://formspree.io/"
            target="_blank"
            rel="noopener noreferrer"
            style={{ textDecoration: 'underline' }}
          >
            Formspree
          </a>{' '}
          to submit the contact form to our official secretary email address.
          Formspree may collect and store personal information, such as your
          name and email address.{' '}
          <a
            href="https://docs.formspree.io/files/privacy.pdf"
            target="_blank"
            rel="noopener noreferrer"
            style={{ textDecoration: 'underline' }}
          >
            Formspree privacy policy
          </a>
        </p>
        <p>
          We only ask for personal information (i.e. name, email address) to
          provide a contact service to you. We collect it by fair and lawful
          means, with your knowledge and consent. We also let you know why were
          collecting it and how it will be used.
        </p>
        <p>
          We only retain collected information for as long as necessary to
          provide you with your requested service. What data we store, well
          protect within commercially acceptable means to prevent loss and
          theft, as well as unauthorised access, disclosure, copying, use or
          modification.
        </p>
        <p>
          We dont share any personally identifying information publicly or with
          third-parties, except when required to by law.
        </p>
        <p>
          Our website may link to external sites that are not operated by us.
          Please be aware that we have no control over the content and practices
          of these sites, and cannot accept responsibility or liability for
          their respective privacy policies.
        </p>
        <p>
          You are free to refuse our request for your personal information, with
          the understanding that we may be unable to provide you with some of
          your desired services.
        </p>
        <p>
          Your continued use of our website will be regarded as acceptance of
          our practices around privacy and personal information. If you have any
          questions about how we handle user data and personal information, feel
          free to contact us.
        </p>
        <p>This policy is effective as of 25 September 2019.</p>
      </div>
    </div>
  );
};

export default PrivacyPage;
